import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const URL_PATH = "/hh";

// Colors
const BG_COLOR_1 = "#a3a3c2" //dusty blue

const BORDER_STYLE = `3px solid ${BG_COLOR_1}`;

const TEXT_STYLE = {
  fontSize: "13px",
  textAlign: "center",
}

const TEXT_STYLE2 = {
  fontSize: "13px",
  textAlign: "justify",
}

const TEXT_STYLE3 = {
  fontSize: "13px",
  textAlign: "left",
}

const TEXT_STYLE4 = {
  fontSize: "10px",
  textAlign: "center",
}

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 50px;
  border: none;
  background-color: var(--secondary);
  padding: 10px;
  font-weight: bold;
  color: var(--secondary-text);
  width: 100px;
  cursor: pointer;
  box-shadow: 0px 6px 0px -0px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 5px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 5px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const StyledRoundButton = styled.button`
  padding: 10px;
  border-radius: 100%;
  border: none;
  background-color: var(--primary);
  padding: 10px;
  font-weight: bold;
  font-size: 13px;
  color: var(--primary-text);
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 40%;
`;

export const StyledLogo = styled.img`
  width: 200px;
  @media (min-width: 767px) {
    width: 300px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

// Circle Image 
export const StyledImg = styled.img`
  box-shadow: 0px 5px 11px 2px rgba(0, 0, 0, 0.5);
  border: ${BORDER_STYLE};
  background-color: var(--accent);
  border-radius: 100%;
  width: 150px;
  transition: width 0.5s;
`;

export const StyledLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
`;

const buttonStyle = {
  backgroundColor: BG_COLOR_1,
  border: "none",
  color: "white", // text color
  padding: "3px 15px",
  textAlign: "center",
  display: "inline-block",
  fontSize: "13px",
  margin: "4px 24px",
  cursor: "pointer",
  borderRadius: "9px" //roundess
}

const getDynamicCost = (supply) => {
  if (supply < 2000)
    return "0.02";
  else if (supply < 5000)
    return "0.03";
  else if (supply < 7500)
    return "0.04";
  else if (supply < MAX_SUPPLY)
    return "0.05";
  return "0.06"
}

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(`Click MINT to get your NFT.`);
  const [mintAmount, setMintAmount] = useState(1);
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const [currentText, setCurrentText] = useState("about");

  const claimNFTs = () => {
    let cost = CONFIG.WEI_COST;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `WOW, the ${CONFIG.NFT_NAME} is yours! Visit Opensea.io to view it.`
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 10) {
      newMintAmount = 10;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch(`${URL_PATH}/config/config.json`, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  }, []);

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  const renderText = (input) => {
    console.log(input);
    if (input === "about")
      return (
        <>
          <s.SpacerSmall />
          <p style={TEXT_STYLE}>Hi there!</p>
          <s.SpacerMedium />
          <p style={TEXT_STYLE2}>We are a collection of 10,000 unique HH NFTs placed on the Ethereum blockchain as collectibles. 
            Your mints will be gas-efficient, as HH is based on a low gas ERC721 contract (yay!).
            Originally conceptualized as a set of mini stories, HH will instead make its debut on the NFT platform as we want to reach out to people 
            who are unafraid to step into the future. Let's have our stories reach the hearts of many after - forever embedded into the blockchain.
          </p>
          <s.SpacerMedium />
          <p style={TEXT_STYLE}>Come join Hollow's adventures now:</p>
        </>
      )
    if (input === "How to Mint")
      return (
        <div style={{width: "100%"}}>
          <s.SpacerSmall />
          <p style={TEXT_STYLE}>Everything is simple here.</p>
          <s.SpacerMedium />
          <div style={{display: "flex", flexFlow: "row nowrap"}}>
            <p style={{...TEXT_STYLE3, marginRight: "8px"}}>1.
            </p>
            <p style={TEXT_STYLE3}>
              Download metamask wallet. 
              It’s <a href="https://metamask.io/download/">free</a>!
              Put ethereum in it - enough for  NFT price + gas fees.
              &nbsp;(Gas fees are like postage fees when you mail stuff. It’s payment for the service. 
              Check <a href="https://etherscan.io/gastracker">here</a> to track gas prices.)
            </p>
          </div>
          <s.SpacerXSmall />
          <div style={{display: "flex", flexFlow: "row nowrap"}}>
            <p style={{...TEXT_STYLE3, marginRight: "8px"}}>2. 
            </p>
            <p style={TEXT_STYLE3}>Click on the yellow &nbsp;“CONNECT”&nbsp; button below and follow metamask's prompts. That’s it. Now you’re the proud owner of our NFT.</p>
            </div>

          </div>
      )
    if (input === "Stories")
      return (
        <>
          <s.SpacerSmall />
          <p style={TEXT_STYLE}>You are here at our beginning.</p>
          <s.SpacerMedium />
          <p style={TEXT_STYLE2}>We’re working on our first story to share with the community.  Here’s a little sneak peak: the title is, ‘ Secret to Longevity’. 
          What do you think Hollow might discover? — Oh golly we can’t wait to tell you!
            <s.SpacerMedium /></p>
          <p style={TEXT_STYLE2}>It’s going to be a casual style picture-book. 
          We’re just small dreamers hoping that others will like our little tales. 
            Well, in the meantime you may find us at our socials below. 
          </p>
          <s.SpacerSmall />
          <p style={TEXT_STYLE}>Instagram: <a href="https://www.instagram.com/hh.hellohollow/">@hh.hellohollow</a></p>
          <p style={TEXT_STYLE}>Twitter  : <a href="https://twitter.com/hellohollow_hh">@hellohollow_hh</a></p>
          <p style={TEXT_STYLE}>Facebook  : <a href="https://www.facebook.com/groups/662197298312639">Hello Hollow NFT Group</a></p>
          <s.SpacerMedium />
          <p style={TEXT_STYLE}>See you there!</p>
         
        </>
      )
  }

  return (
    <div
      style={{ display: "flex", padding: "0px 50px", width: "100%", flexDirection: "row", justifyContent: "center" }}
    // style={{ backgroundColor: "var(--primary)" }}
    // image={CONFIG.SHOW_BACKGROUND ? "/config/images/bg.png" : null}
    >
      <div
        style={{ display: "flex", padding: "0px 50px", width: "100%", maxWidth: "800px", flexDirection: "column", alignItems: "center" }}
      >
        <div id="rectangleBg" style={{
          position: "absolute",
          zIndex: -1,
          width: "100%",
          height: "130px",
          backgroundColor: BG_COLOR_1
        }} />
        <StyledImg style={{ marginTop: "20px" }} alt={"example"} src={`${URL_PATH}/config/images/50gif.gif`} />

        {/* main logo*/}
        <img
    src={`${URL_PATH}/config/images/main logo.svg`}
    alt="Hello Hollow main logo"
    height="87"
    width="300" />


        <div style={{ display: "flex" }}>
          <button
            style={{ ...buttonStyle, backgroundColor: currentText === "about" ? "#7c7c8b" : "#a3a3c2" }}
            onClick={() => setCurrentText("about")}
          >
            About
          </button>

          <button
            style={{ ...buttonStyle, backgroundColor: currentText === "How to Mint" ? "#7c7c8b" : "#a3a3c2" }}
            onClick={() => setCurrentText("How to Mint")}
          >
             How to Mint
          </button>

          <button
            style={{ ...buttonStyle, backgroundColor: currentText === "Stories" ? "#7c7c8b" : "#a3a3c2" }}
            onClick={() => setCurrentText("Stories")}
          >
            Stories
          </button>

        </div>
        <s.SpacerMedium />

        {/*description text */}

        {renderText(currentText)}
        <ResponsiveWrapper flex={1} style={{
          width: "100%",
        }} test>
          <s.SpacerLarge />
          <s.Container
            flex={2}
            jc={"center"}
            ai={"center"}
            style={{
              backgroundColor: "var(--accent)",
              padding: 1,
              borderRadius: 30,
              border: BORDER_STYLE,
              boxShadow: "0px 5px 11px 2px rgba(0,0,0,0)",
            }}
          >
            <s.TextTitle
              style={{
                textAlign: "center",
                fontSize: 50,
                fontWeight: "bold",
                color: "var(--accent-text)",
              }}
            >
              {data.totalSupply} / {CONFIG.MAX_SUPPLY}
            </s.TextTitle>
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--primary-text)",
              }}
            >
              <StyledLink target={"_blank"} href={CONFIG.SCAN_LINK}>
                {truncate(CONFIG.CONTRACT_ADDRESS, 15)}
              </StyledLink>
            </s.TextDescription>
            <s.SpacerSmall />
            {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  All HH have been sold out here, but
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  You can still get your {CONFIG.NFT_NAME} on
                </s.TextDescription>
                <s.SpacerSmall />
                <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                  {CONFIG.MARKETPLACE}
                </StyledLink>
              </>
            ) : (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  1 {CONFIG.SYMBOL} is currently {getDynamicCost(data.totalSupply)}{" "}
                  {CONFIG.NETWORK.SYMBOL}.
                </s.TextTitle>
                <s.SpacerXSmall />

                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                > <br />Only the first 20% are 0.02 ETH! <br /><br />
                  Subsequently, each HH NFT shall be valued as such:
                  <br />
                  21-50%     0.03 ETH <br />
                  51-75%     0.04 ETH <br />
                  75-100%    0.05 ETH <br /><br /><br />

                </s.TextDescription>
                <s.SpacerSmall />
                {blockchain.account === "" ||
                  blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      Connect to the {CONFIG.NETWORK.NAME} network to begin:
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      CONNECT
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--accent-text)",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {feedback}
                    </s.TextDescription>
                    <s.SpacerMedium />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledRoundButton
                        style={{ lineHeight: 0.4 }}
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          decrementMintAmount();
                        }}
                      >
                        -
                      </StyledRoundButton>
                      <s.SpacerMedium />
                      <s.TextDescription
                        style={{
                          textAlign: "center",
                          color: "var(--accent-text)",
                        }}
                      >
                        {mintAmount}
                      </s.TextDescription>
                      <s.SpacerMedium />
                      <StyledRoundButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          incrementMintAmount();
                        }}
                      >
                        +
                      </StyledRoundButton>
                    </s.Container>
                    <s.SpacerSmall />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          claimNFTs();
                          getData();
                        }}
                      >
                        {claimingNft ? "BUSY" : "MINT"}
                      </StyledButton>
                    </s.Container>
                  </>
                )}
              </>
            )}
            <s.SpacerMedium />
          </s.Container>
          <s.SpacerXSmall />
        </ResponsiveWrapper>
        <s.SpacerXSmall />
        <s.Container jc={"center"} ai={"center"} style={{ width: "100%" }}>
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
            }}
          >
            <p style={TEXT_STYLE4}>
            Check that you are connected to the correct network (
            {CONFIG.NETWORK.NAME} Mainnet) and address.
            </p>
          </s.TextDescription>
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
            }}
          >
            <p style={TEXT_STYLE4}>
            We have set the gas limit to {CONFIG.GAS_LIMIT} for your NFT-minting to be a success.
            </p>

          </s.TextDescription>
          <s.SpacerLarge />
        </s.Container>
      </div>
    </div>
  );
}

export default App;
